/* Site Header
---------------------------------------------------------------------------- */

.site-header {
	background-color: #fff;
	box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
	padding: 0 30px;


	@media only screen and (min-width: 960px) {
		position: sticky;
		top: 0;
		z-index: 9999;
	

		.admin-bar & {
			top: 32px;
		}
	}
}