/* Featured Content
--------------------------------------------- */

.featured-content {

    .entry {
	    border-bottom: 1px solid #eee;
	    margin-bottom: 20px;
    

    &:last-of-type {
	        border-bottom: none;
	        margin-bottom: 0;
        }
    }

    .entry-title {
	    font-size: 16px;
	    margin-bottom: 5px;
	    margin-top: 10px;
    }
}